import React, { useEffect, useRef, useState, useMemo } from 'react';
import { createChart, IChartApi, ISeriesApi, CandlestickData, HistogramData, LineData, CandlestickSeries, HistogramSeries, LineSeries } from 'lightweight-charts';
import { CryptoCandle, formatCryptoPrice } from '../../services/cryptoService';

interface CryptoChartProps {
  data: CryptoCandle[];
  symbol: string;
  height?: number;
  showVolume?: boolean;
  showIndicators?: boolean;
  onCrosshairMove?: (data: any) => void;
}

export const CryptoChart: React.FC<CryptoChartProps> = ({
  data,
  symbol,
  height = 600,
  showVolume = true,
  showIndicators = true,
  onCrosshairMove
}) => {
  const chartContainerRef = useRef<HTMLDivElement>(null);
  const chartRef = useRef<IChartApi | null>(null);
  const candlestickSeriesRef = useRef<ISeriesApi<'Candlestick'> | null>(null);
  const volumeSeriesRef = useRef<ISeriesApi<'Histogram'> | null>(null);
  const sma20SeriesRef = useRef<ISeriesApi<'Line'> | null>(null);
  const sma50SeriesRef = useRef<ISeriesApi<'Line'> | null>(null);
  
  const [currentCandle, setCurrentCandle] = useState<CryptoCandle | null>(null);

  // Calculate technical indicators
  const technicalData = useMemo(() => {
    if (!data.length) return { sma20: [], sma50: [] };

    const sma20: LineData[] = [];
    const sma50: LineData[] = [];

    // Calculate SMA 20
    for (let i = 19; i < data.length; i++) {
      const sum = data.slice(i - 19, i + 1).reduce((acc, candle) => acc + candle.close, 0);
      sma20.push({
        time: data[i].time as any,
        value: sum / 20
      });
    }

    // Calculate SMA 50
    for (let i = 49; i < data.length; i++) {
      const sum = data.slice(i - 49, i + 1).reduce((acc, candle) => acc + candle.close, 0);
      sma50.push({
        time: data[i].time as any,
        value: sum / 50
      });
    }

    return { sma20, sma50 };
  }, [data]);

  // Initialize chart
  useEffect(() => {
    if (!chartContainerRef.current) return;

    const chart = createChart(chartContainerRef.current, {
      layout: {
        background: { color: '#111827' },
        textColor: '#d1d5db',
        fontSize: 12,
        fontFamily: 'Inter, system-ui, sans-serif'
      },
      grid: {
        vertLines: { color: '#374151', style: 1, visible: true },
        horzLines: { color: '#374151', style: 1, visible: true },
      },
      crosshair: {
        mode: 1,
        vertLine: {
          color: '#6b7280',
          width: 1,
          style: 2,
          labelBackgroundColor: '#1f2937'
        },
        horzLine: {
          color: '#6b7280',
          width: 1,
          style: 2,
          labelBackgroundColor: '#1f2937'
        }
      },
      rightPriceScale: {
        borderColor: '#374151',
        textColor: '#d1d5db',
        entireTextOnly: true,
      },
      timeScale: {
        borderColor: '#374151',
        timeVisible: true,
        secondsVisible: false,
      }
    });

    chartRef.current = chart;

    // Create candlestick series
    const candlestickSeries = chart.addSeries(CandlestickSeries, {
      upColor: '#10b981',
      downColor: '#ef4444',
      borderVisible: false,
      wickUpColor: '#10b981',
      wickDownColor: '#ef4444',
      priceFormat: {
        type: 'price',
        precision: 8,
        minMove: 0.00000001,
      },
    });

    candlestickSeriesRef.current = candlestickSeries;

    // Create volume series if enabled
    if (showVolume) {
      const volumeSeries = chart.addSeries(HistogramSeries, {
        color: '#6b7280',
        priceFormat: {
          type: 'volume',
        },
        priceScaleId: 'volume',
      });

      volumeSeriesRef.current = volumeSeries;

      // Configure volume price scale
      chart.priceScale('volume').applyOptions({
        scaleMargins: {
          top: 0.7,
          bottom: 0,
        },
        borderColor: '#374151',
        textColor: '#9ca3af',
      });
    }

    // Create SMA indicators if enabled
    if (showIndicators) {
      const sma20Series = chart.addSeries(LineSeries, {
        color: '#3b82f6',
        lineWidth: 1,
        title: 'SMA 20',
        priceLineVisible: false,
        crosshairMarkerVisible: false,
      });

      const sma50Series = chart.addSeries(LineSeries, {
        color: '#f59e0b',
        lineWidth: 1,
        title: 'SMA 50',
        priceLineVisible: false,
        crosshairMarkerVisible: false,
      });

      sma20SeriesRef.current = sma20Series;
      sma50SeriesRef.current = sma50Series;
    }

    // Handle crosshair move
    chart.subscribeCrosshairMove((param) => {
      if (!param.time) {
        setCurrentCandle(null);
        return;
      }

      const candleData = param.seriesData.get(candlestickSeries) as CandlestickData | undefined;
      const volumeData = volumeSeriesRef.current ? 
        param.seriesData.get(volumeSeriesRef.current) as HistogramData | undefined : 
        undefined;

      if (candleData) {
        setCurrentCandle({
          time: param.time as number,
          open: candleData.open,
          high: candleData.high,
          low: candleData.low,
          close: candleData.close,
          volume: volumeData?.value || 0,
          vwap: 0, // Not available in crosshair
          trades: 0 // Not available in crosshair
        });

        onCrosshairMove?.({
          time: param.time,
          candle: candleData,
          volume: volumeData?.value,
          price: candleData.close
        });
      }
    });

    // Handle resize
    const handleResize = () => {
      if (chartContainerRef.current && chartRef.current) {
        chartRef.current.applyOptions({
          width: chartContainerRef.current.clientWidth,
          height: height
        });
      }
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (chartRef.current) {
        chartRef.current.remove();
        chartRef.current = null;
      }
    };
  }, [showVolume, showIndicators, symbol, height, onCrosshairMove]);

  // Update chart data
  useEffect(() => {
    if (!data.length || !chartRef.current) return;

    const candlestickData: CandlestickData[] = data.map(candle => ({
      time: candle.time as any,
      open: candle.open,
      high: candle.high,
      low: candle.low,
      close: candle.close
    }));

    const volumeData: HistogramData[] = data.map(candle => ({
      time: candle.time as any,
      value: candle.volume,
      color: candle.close >= candle.open ? '#10b981' : '#ef4444'
    }));

    // Update series data
    if (candlestickSeriesRef.current) {
      candlestickSeriesRef.current.setData(candlestickData);
    }

    if (volumeSeriesRef.current && showVolume) {
      volumeSeriesRef.current.setData(volumeData);
    }

    if (sma20SeriesRef.current && showIndicators) {
      sma20SeriesRef.current.setData(technicalData.sma20);
    }

    if (sma50SeriesRef.current && showIndicators) {
      sma50SeriesRef.current.setData(technicalData.sma50);
    }

    // Fit content to visible area
    chartRef.current.timeScale().fitContent();
  }, [data, showVolume, showIndicators, technicalData]);

  // Update chart size
  useEffect(() => {
    if (chartRef.current && chartContainerRef.current) {
      chartRef.current.applyOptions({
        width: chartContainerRef.current.clientWidth,
        height: height
      });
    }
  }, [height]);

  const getCurrentPrice = () => {
    if (currentCandle) return currentCandle.close;
    if (data.length > 0) return data[data.length - 1].close;
    return 0;
  };

  const getCurrentChange = () => {
    if (data.length < 2) return { change: 0, changePercent: 0 };
    
    const current = getCurrentPrice();
    const previous = data[data.length - 2]?.close || 0;
    const change = current - previous;
    const changePercent = previous > 0 ? (change / previous) * 100 : 0;
    
    return { change, changePercent };
  };

  const { change, changePercent } = getCurrentChange();
  const currentPrice = getCurrentPrice();

  return (
    <div className="w-full bg-gray-900 rounded-lg overflow-hidden">
      {/* Chart Header */}
      <div className="flex items-center justify-between p-4 border-b border-gray-800">
        <div className="flex items-center space-x-6">
          <h2 className="text-xl font-bold text-white">{symbol}</h2>
          <div className="flex items-baseline space-x-3">
            <span className="text-2xl font-bold text-white">
              {formatCryptoPrice(currentPrice)}
            </span>
            <span className={`text-lg font-semibold ${
              change >= 0 ? 'text-green-400' : 'text-red-400'
            }`}>
              {change >= 0 ? '+' : ''}{formatCryptoPrice(Math.abs(change))}
            </span>
            <span className={`text-lg font-semibold ${
              change >= 0 ? 'text-green-400' : 'text-red-400'
            }`}>
              ({changePercent.toFixed(2)}%)
            </span>
          </div>
        </div>
        
        <div className="flex items-center space-x-4 text-sm text-gray-400">
          {currentCandle && (
            <>
              <span>O: {formatCryptoPrice(currentCandle.open)}</span>
              <span>H: {formatCryptoPrice(currentCandle.high)}</span>
              <span>L: {formatCryptoPrice(currentCandle.low)}</span>
              <span>C: {formatCryptoPrice(currentCandle.close)}</span>
              {currentCandle.volume > 0 && (
                <span>Vol: {currentCandle.volume.toLocaleString()}</span>
              )}
            </>
          )}
          {!currentCandle && data.length > 0 && (
            <span>Candles: {data.length}</span>
          )}
        </div>
      </div>

      {/* Chart Container */}
      <div 
        ref={chartContainerRef}
        className="w-full"
        style={{ height: `${height}px` }}
      />

      {/* Legend */}
      {showIndicators && (
        <div className="flex items-center space-x-4 p-3 bg-gray-800 border-t border-gray-700 text-xs">
          <div className="flex items-center space-x-1">
            <div className="w-3 h-0.5 bg-blue-500"></div>
            <span className="text-blue-400">SMA 20</span>
          </div>
          <div className="flex items-center space-x-1">
            <div className="w-3 h-0.5 bg-yellow-500"></div>
            <span className="text-yellow-400">SMA 50</span>
          </div>
          {showVolume && (
            <div className="flex items-center space-x-1">
              <div className="w-3 h-2 bg-gray-500"></div>
              <span className="text-gray-400">Volume</span>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default CryptoChart;